.container {
  display: flex;
  flex-direction: column;
}

.heading {
  color: rgba(7,27,82,1)
}

.actions {
  display: flex;
  justify-content: space-around;
}

.button {
  cursor: pointer;
  width: 10em;
  padding: .6em;
  border-radius: 10px;
  transition: all .3s ease-in-out;
}

.button:hover {
  transform: scale(1.1);
}

.button:disabled {
  background: #e7040f;
  transition: none;
  transform: none;
  cursor: default;
}

@media only screen and (max-width: 500px) {
  .actions {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
  }
}
